.project {
	@include helvetica;
	color: white;
	width: 50%;
	max-width: calc(50% - 16px);
	margin-top: 30px;
	border-radius: 10px;
	min-height: 328px;
	display: flex;
	flex-direction: column;
	padding: 17px 40px 47px;
	@include md {
		padding-bottom: 27px; }
	@include sm {
		padding: 17px 22px 27px;
		width: 100%;
		max-width: 100%; }

	&__title {}

	&__logo {
		margin-top: auto; }

	&__image {}

	&__footer {
		margin-top: 12px;
		display: flex;
		align-items: baseline; }

	&__date {
		font-size: 20px;
		font-weight: 300; }

	&__link {
		margin-left: auto;
		color: white;
		text-decoration: underline;
		@include hover {
			opacity: .7;
			text-decoration: none; } } }
